// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import wx from 'weixin-js-sdk'

// 引入自定义组件。index.js是组件的默认入口
import Loading from './components/loading'
//console.log(Loading)
Vue.use(Loading);

Vue.prototype.$http = axios;
Vue.config.productionTip = false

Vue.prototype.wxShare = function (title, desc, link,image='http://static.samured.com/assets/images/video/cover/iQVBBrK_gEM.jpg') {

    link = location.href;
    var url = window.location.href.split('#')[0];
    var paths = window.location.href.split('#')[1]
    var path1 = paths.split('?')[0].substring(1)||'';
    var path2 = paths.split('?')[1]||'';
    var link=url+(path1?'?path='+path1+(path2?'path1='+path2:''):'')
    console.log(url)
    console.log(paths)
    console.log(path1)
    console.log(path2)
    console.log(link)
    alert(link)
    // var url = encodeURIComponent(url);
    //请求获取微信签名信息
    axios('/test',{params:{url:url}}).then(res => {
        // 获得签名配置
        var Data = res.data;
        wx.config({
            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: 'wx1a2b42155c618d7a',   // 必填，公众号的唯一标识
            timestamp: Data.timesstr, // 必填，生成签名的时间戳
            nonceStr: Data.noncestr,   // 必填，生成签名的随机串
            signature: Data.signature, // 必填，签名，见附录1
            jsApiList: ['onMenuShareAppMessage', 'onMenuShareTimeline','onMenuShareQQ','onMenuShareQZone'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
        });
    })

    wx.ready(() => {
        wx.onMenuShareAppMessage({ // 分享给朋友
          title: title, // 分享标题
          desc: desc,   // 分享描述
          link: link,   // 分享链接 默认以当前链接
          imgUrl: image,// 分享图标
          // 用户确认分享后执行的回调函数
          success: function () {
            //alert('成功')
          },
          // 用户取消分享后执行的回调函数
          cancel: function () {
            //alert('分享到朋友取消')
            //console.log('分享到朋友取消');
          }
        });
        //分享到朋友圈
        wx.onMenuShareTimeline({
            title: title, // 分享标题
            desc: desc,
            link: link,
            imgUrl: image,
            // 用户确认分享后执行的回调函数
            success: function () {
                //alert('成功')
            },
            // 用户取消分享后执行的回调函数
            cancel: function () {
                //alert('分享到朋友圈取消')
                //console.log('分享到朋友圈取消');
            }
        });

        //分享到qq
        wx.onMenuShareQQ({
            title: title, // 分享标题
            desc: desc,
            link: link,
            imgUrl: image,
            // 用户确认分享后执行的回调函数
            success: function () {
                //alert('成功')
            },
            // 用户取消分享后执行的回调函数
            cancel: function () {
                //alert('分享到朋友圈取消')
                //console.log('分享到朋友圈取消');
            }
        });

        //分享到qq空间
        wx.onMenuShareQZone({
            title: title, // 分享标题
            desc: desc,
            link: link,
            imgUrl: image,
            // 用户确认分享后执行的回调函数
            success: function () {
                //alert('成功')
            },
            // 用户取消分享后执行的回调函数
            cancel: function () {
                //alert('分享到朋友圈取消')
                //console.log('分享到朋友圈取消');
            }
        });
    });
    wx.error(function(res){
        // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，
        //console.log(res)
    });
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



